export default {
  data: function () {
    return {
      remainHeight:100//剩余高度
    }
  },
  mounted: function () {
    this.$nextTick(function () {
      this.remainHeight =  window.innerHeight - window.document.getElementById('header').clientHeight -window.document.getElementById('menu').clientHeight ;
    })
  }
}
