

import Menu from '@/components/Menu'
import Header from '@/components/head/Header'
import Credit from '@/components/Credit'
import RemainSpace from '@/components/RemainSpace'
import Divider from 'vux/src/components/divider/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'



export default {
name: 'index',
components: {
Menu,
Header,
Credit,
Divider,
Scroller,
RemainSpace
},
data: function () {
return {
creditSort: 425, // 用户积分
recommendClass: '车载配件', // 推荐商品类型
recommendClassUrl: '/showMore', // 推荐商品链接
recommendImgUrl: '/img/banner.png', // 推荐商品图片
loading: false, // 加载
isFinish:false,
goods: [],
num: 4
}
},
mounted: function () {
this.$nextTick(function () {




this.loadMore ()
})
},
beforeDestroy: function() {

},
methods: {
toRecommendClass () {
this.$router.push({name:'showMore',query:{clzName:this.recommendClass}})
},

loadMore () {
let that = this;
if(!that.isFinish && !this.loading ){
this.loading = true
setTimeout(() => {
let last = this.goods[this.goods.length - 1]
for (let i = 1; i <= 9; i++) {
if(i%3==0){
this.goods.push({
id: that.num++,
name: '车载配件' + i,
originalPrice:1108,
price: 1058,
img: '/img/good/'+i+'.jpg'
})
}else if(i%3==1){
this.goods.push({
id: that.num++,
name: '车载配件' + i,
originalPrice:1304,
price: 1058,
img: '/img/good/'+i+'.jpg'
})
}else {
this.goods.push({
id: that.num++,
name: '车载配件' + i,
originalPrice:1406,
price: 1058,
img: '/img/good/'+i+'.jpg'
})
}


}
this.loading = false

if(this.num>50){
that.isFinish = true;
}

}, 2000)
}
},

goodDetail(goodId){
this.$router.push({name:'good',query:{goodId:goodId}})
}

}

}

